@-moz-keyframes spin {
    100% {
      -moz-transform: rotate(360deg);
    }
  }
  @-webkit-keyframes spin {
    100% {
      -webkit-transform: rotate(360deg);
    }
  }
  @keyframes spin {
    100% {
      -webkit-transform: rotate(360deg);
      transform: rotate(360deg);
    }
  }
  
  @-moz-keyframes pulse {
    50% {
      -moz-transform: scale(0.96);
    }
    100% {
      -moz-transform: scale(1);
    }
  }
  @-webkit-keyframes pulse {
    50% {
      -webkit-transform: scale(0.96);
    }
    100% {
      -webkit-transform: scale(1);
    }
  }
  @keyframes pulse {
    50% {
      -webkit-transform: scale(0.96);
      transform: scale(0.96);
    }
    100% {
      -webkit-transform: scale(1);
      transform: scale(1);
    }
  }
  
  @keyframes scrolling {
    50% {
      background-position: -50px -150%;
    }
    100% {
      background-position: -100px -300%;
    }
  }
  
  .sun {
    position: absolute;
    top: -8%;
    right: -12%;
    transition: transform 1s, opacity 2s, width 2s, top 1s, right 1s;
    width: 30%;
    -webkit-animation: spin 25s linear infinite;
    -moz-animation: spin 25s linear infinite;
    animation: spin 25s linear infinite;
  }
  
  .sun-hidden {
    top: 14rem;
    right: -30rem;
    opacity: 0;
  }
  
  .sun-small {
    width: 25%;
  }
  
  .sun-big {
    width: 35%;
  }
  
  .cloud {
    position: absolute;
    top: 1%;
    transition: opacity 5s;
    width: 30%;
    -webkit-animation: pulse 4s linear infinite;
    -moz-animation: pulse 4s linear infinite;
    animation: pulse 4s linear infinite;
  }
  
  .cloud-dark {
    filter: brightness(50%);
  }
  
  .cloud-1 {
    transition: all 0.8s;
    left: 1%;
  }
  
  .cloud-2 {
    transition: all 2s;
    left: 21%;
  }
  
  .cloud-3 {
    transition: all 1.2s;
    left: 36%;
  }
  
  .cloud-4 {
    transition: all 2.5s;
    left: 56%;
  }
  
  .cloud-5 {
    transition: all 2.7s;
    left: 69%;
  }
  
  .hidden {
    left: -30rem !important;
    transition: all 2s;
    opacity: 0;
  }
  
  .rain {
    position: absolute;
    top: 10%;
    width: 150%;
    height: 80%;
    right: -5%;
    opacity: 0.6;
    transition: all 1.2s;
    background-size: 110%;
    background-repeat: repeat;
    animation: scrolling 4s linear infinite reverse; 
  }
  
  .rain-hidden {
    transform: translateY(-80rem);
    opacity: 0;
  }
  
  .display-filter {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90.15%;
    background-color: black;
    opacity: 0;
    transition: all 1s;
    background-size: cover;
    border-radius: 10px 10px 10px 10px;
    z-index: 0;
  }
  
  @media only screen and (max-width: 650px) {
    .display-filter {
      border-radius: 0;
    }
  }
  
  .display-filter-dark {
    opacity: 0.19;
  }
  
  .display-filter-darker {
    opacity: 0.26;
  }
  
  