@import url(https://fonts.googleapis.com/css?family=PT+Sans:400,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(90deg, #00c6ff 10%, #0072ff 90%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;  
  font-family: 'PT Sans', Helvetica, Arial, sans-serif;
  height: 100%;
}

.App-header {
  height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  margin-bottom: 3vh;
}

.bottom-nav {
  position: fixed;
  width: 100%;
  height: 3rem;
  bottom: 0;
  text-align: center;
  justify-content: center;
  font-family: sans-serif;
  z-index: 5;
  font-size: 1rem;
  margin-top: 2rem;
}

.bottom-nav a,
.bottom-nav a:visited {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.7);
  margin-right: 5rem;
  font-size: 1rem;
  transition: all 0.2s;
  margin: 0 2rem;
}

.bottom-nav a:hover,
.bottom-nav a:visited:hover {
  color: #fff;
  transform: scale(1.1);
}

.city-form {
  width: 100%;
  height: 10%;
  box-sizing: border-box;
}
input {
  box-sizing: border-box;
  font-size: 13px;
  vertical-align: top;
}
input:focus {
  outline: none;
  box-shadow: none;
}
.search {
  padding: 0 20px;
  font-size: 18px;
  width: 75%;
  max-width: 500px;
  height: 50px;
  border: 1px solid #b3b3b3;
  border-radius: 20px 0 0 20px;
}
.search:focus {
  outline: none;
  box-shadow: none;
}
.submit {
  cursor: pointer;
  border: none;
  background: url(data:image/png;base64,) no-repeat center center,
    #1e1e20;
  background-size: 30px 30px;
  border-radius: 0 20px 20px 0;
  padding: 15px 25px;
  display: inline-block;
  width: 60px;
  height: 50px;
}

.wrapper {
  width: 375px;
  height: 550px;
  background: white;
  margin: auto;
  position: relative;
  overflow: hidden;
  border-radius: 10px 10px 10px 10px;
  box-shadow: 0;
  transform: scale(0.95);
  transition: box-shadow 0.5s, transform 0.5s;
}
.wrapper:hover {
  transform: scale(1);
  box-shadow: 5px 20px 30px rgba(0, 0, 0, 0.2);
}
.wrapper .container {
  width: 100%;
  height: 100%;
}
.wrapper .container .top {
  height: 80%;
  width: 100%;
  background-size: 100%;
}
.city-vector {
  height: 118%;
  width: 100%;
  z-index: 1;
}
.wrapper .container .bottom {
  width: 100%;
  height: 20%;
  background: #ffffff;
  position: relative;
  transition: transform 0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrapper .container .bottom p {
  margin: 0;
  padding: 0;
}

@-webkit-keyframes spin {
    100% {
      -webkit-transform: rotate(360deg);
    }
  }
  @keyframes spin {
    100% {
      transform: rotate(360deg);
    }
  }
  @-webkit-keyframes pulse {
    50% {
      -webkit-transform: scale(0.96);
    }
    100% {
      -webkit-transform: scale(1);
    }
  }
  @keyframes pulse {
    50% {
      transform: scale(0.96);
    }
    100% {
      transform: scale(1);
    }
  }
  
  @-webkit-keyframes scrolling {
    50% {
      background-position: -50px -150%;
    }
    100% {
      background-position: -100px -300%;
    }
  }
  
  @keyframes scrolling {
    50% {
      background-position: -50px -150%;
    }
    100% {
      background-position: -100px -300%;
    }
  }
  
  .sun {
    position: absolute;
    top: -8%;
    right: -12%;
    transition: transform 1s, opacity 2s, width 2s, top 1s, right 1s;
    width: 30%;
    -webkit-animation: spin 25s linear infinite;
    animation: spin 25s linear infinite;
  }
  
  .sun-hidden {
    top: 14rem;
    right: -30rem;
    opacity: 0;
  }
  
  .sun-small {
    width: 25%;
  }
  
  .sun-big {
    width: 35%;
  }
  
  .cloud {
    position: absolute;
    top: 1%;
    transition: opacity 5s;
    width: 30%;
    -webkit-animation: pulse 4s linear infinite;
    animation: pulse 4s linear infinite;
  }
  
  .cloud-dark {
    -webkit-filter: brightness(50%);
            filter: brightness(50%);
  }
  
  .cloud-1 {
    transition: all 0.8s;
    left: 1%;
  }
  
  .cloud-2 {
    transition: all 2s;
    left: 21%;
  }
  
  .cloud-3 {
    transition: all 1.2s;
    left: 36%;
  }
  
  .cloud-4 {
    transition: all 2.5s;
    left: 56%;
  }
  
  .cloud-5 {
    transition: all 2.7s;
    left: 69%;
  }
  
  .hidden {
    left: -30rem !important;
    transition: all 2s;
    opacity: 0;
  }
  
  .rain {
    position: absolute;
    top: 10%;
    width: 150%;
    height: 80%;
    right: -5%;
    opacity: 0.6;
    transition: all 1.2s;
    background-size: 110%;
    background-repeat: repeat;
    animation: scrolling 4s linear infinite reverse; 
  }
  
  .rain-hidden {
    transform: translateY(-80rem);
    opacity: 0;
  }
  
  .display-filter {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 90.15%;
    background-color: black;
    opacity: 0;
    transition: all 1s;
    background-size: cover;
    border-radius: 10px 10px 10px 10px;
    z-index: 0;
  }
  
  @media only screen and (max-width: 650px) {
    .display-filter {
      border-radius: 0;
    }
  }
  
  .display-filter-dark {
    opacity: 0.19;
  }
  
  .display-filter-darker {
    opacity: 0.26;
  }
  
  
.weather-data {
  display: grid;
  grid-gap: 0;
  grid-template-columns: [col1-start] 95px [col2-start] 75px [col3-start] 75px [col4-start] 130px [col4-end];
  grid-template-rows: [row1-start] auto [row2-start] auto [row2-end];
  background-color: #fff;
  color: #fff;
}

.box {
  background-color: #fff;
  color: #000;
  padding: 9px;
  font-size: 150%;
}

.city-name {
  grid-column: col1-start / col4-start;
  grid-row: row1-start;
  text-transform: uppercase;
  font-size: 1.4rem;
  letter-spacing: 0.1rem;
  flex-direction: column;
  font-weight: 400;
  padding-bottom: 0;
}

.temperature {
  grid-column: col4-start;
  grid-row: row1-start / row2-end;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3.5rem;
  font-weight: 400;
  letter-spacing: 0.1rem;
  flex-direction: column-reverse;
}

.font-size {
  font-size: 0.5rem;
}
.humidity{
  grid-column: col1-start;
  grid-row: row2-start;
  text-align: start;
  font-size: 1rem;
  display: flex;
  align-items: center;
  flex-basis: 100%;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

.min-temp {
  grid-column: col2-start;
  grid-row: row2-start;
  text-align: start;
  font-size: 1rem;
  display: flex;
  align-items: center;
  flex-basis: 100%;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}
.max-temp {
  grid-column: col3-start;
  grid-row: row2-start;
  text-align: start;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1px;
  flex-direction: column;
  text-align: center;
}

.max-min-temp {
  font-size: 0.6rem;
}

.rain-probablity {
  text-transform: capitalize;
  font-size: 1rem;
  letter-spacing: 0;
  padding-top: 1.5rem;
  font-weight: 200;
}
.temperature-text{
  font-size: 0.75rem;
  letter-spacing: 0rem;
}

.loading-message {
    position: absolute;
    top: 10rem;
    left: 50%;
    font-size: 3rem;
    transform: translateX(-50%);
    color: rgba(245, 106, 64, 1);
    opacity: 0;
    background-color: transparent;
    border: 0;
    opacity: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-transform: uppercase;
    z-index: 25;
    font-family: sans-serif;
    letter-spacing: 0.3rem;
  }
  
  .loading-true {
    opacity: 1;
  }
